// 필요한 모듈 임포트
import React, { useState, useEffect } from "react";
import {
  Button,
  TextField,
  Paper,
  Checkbox,
  FormControlLabel,
  Grid,
  Box
} from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";
import Fade from "@material-ui/core/Fade";
import Swal from "sweetalert2";
import axios from "axios";

// 컴포넌트에 대한 스타일 정의
const useStyles = makeStyles((theme) => ({
  paper: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    padding: theme.spacing(2)
  },
  form: {
    width: "100%",
    marginTop: theme.spacing(1)
  },
  passwordField: {
    margin: theme.spacing(-4, 0, 2)
  },
  submit: {
    margin: theme.spacing(3, 0, 1)
  },
  buttonGroup: {
    marginTop: theme.spacing(1)
  },
  adminMargin: {
    marginBottom: theme.spacing(5)
  }
}));

// SignIn 컴포넌트의 메인 함수
export default function SignIn() {
  // 컴포넌트에 대한 클래스 정의
  const classes = useStyles();

  // 상태 변수 정의
  const [dong, setDong] = useState("");
  const [ho, setHo] = useState("");
  const [password, setPassword] = useState("");
  const [adminMode, setAdminMode] = useState(false);

  // 'dong' 상태 변수를 변경하는 핸들러 함수
  const handleDongChange = (e) => {
    const { value } = e.target;

    // 입력값이 숫자인지 확인
    if (/^[0-9]*$/.test(value)) {
      setDong(value);
    } else {
      Swal.fire({
        icon: "error",
        title: "Oops...",
        text: "'동' 입력이 잘못되었습니다. 입력값은 숫자여야 합니다."
      });
    }

    // 입력값이 SQL 인젝션 공격을 시도하는 패턴을 포함하고 있는지 확인
    if (/(--|;|'|"|=|OR|AND)/i.test(value)) {
      Swal.fire({
        icon: "error",
        title: "Security Alert...",
        text: "SQL 인젝션 공격을 시도하는 입력이 감지되었습니다."
      });
    }
  };

  // 'ho' 상태 변수를 변경하는 핸들러 함수
  const handleHoChange = (e) => {
    const { value } = e.target;

    // 입력값이 숫자인지 확인
    if (/^[0-9]*$/.test(value)) {
      setHo(value);
    } else {
      Swal.fire({
        icon: "error",
        title: "Oops...",
        text: "'호'수 입력이 잘못되었습니다. 입력값은 숫자여야 합니다."
      });
    }

    // 입력값이 SQL 인젝션 공격을 시도하는 패턴을 포함하고 있는지 확인
    if (/(--|;|'|"|=|OR|AND)/i.test(value)) {
      Swal.fire({
        icon: "error",
        title: "Security Alert...",
        text: "SQL 인젝션 공격을 시도하는 입력이 감지되었습니다."
      });
    }
  };

  // 'password' 상태 변수를 변경하는 핸들러 함수
  const handlePasswordChange = (e) => {
    setPassword(e.target.value);
  };

  // form submit 이벤트 핸들러 함수
  const handleSubmit = (e) => {
    e.preventDefault();

    // 동, 호, 비밀번호 중 하나라도 누락된 경우 경고 메시지 출력
    if (!dong || !ho || !password) {
      Swal.fire({
        icon: "warning",
        title: "Oops...",
        text: "모든 칸을 입력해 주세요!"
      });
    } else {
      const postData = {
        dong: dong,
        ho: ho,
        pw: password,
        isAdmin: adminMode ? "y" : "n"
      };

      axios
        .post("http://61.81.98.195:3000/", postData)
        .then((response) => {
          // 응답 처리 로직을 여기에 작성합니다.
          console.log(response);
        })
        .catch((error) => {
          // 에러 처리 로직을 여기에 작성합니다.
          console.error(error);
        });
    }
  };

  // 'adminMode' 상태 변수를 관리하는 effect hook
  useEffect(() => {
    if (ho === "0000") {
      setAdminMode(true);
    } else {
      setAdminMode(false);
    }
  }, [ho]);

  // SignIn 폼 반환
  return (
    <Paper className={classes.paper}>
      <form className={classes.form} noValidate>
        <Grid container spacing={2}>
          <Grid item xs={6}>
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="dong"
              label="동"
              name="dong"
              type="text"
              value={dong}
              onChange={handleDongChange}
            />
          </Grid>
          <Grid item xs={6}>
            <TextField
              variant="outlined"
              margin="normal"
              required
              fullWidth
              id="ho"
              label="호"
              name="ho"
              type="text"
              value={ho}
              onChange={handleHoChange}
            />
          </Grid>
        </Grid>
        <Fade in={adminMode}>
          <Box className={adminMode ? classes.adminMargin : null}>
            <FormControlLabel
              control={
                <Checkbox checked={adminMode} name="adminCheckbox" disabled />
              }
              label="Login Admin"
            />
          </Box>
        </Fade>

        <TextField
          variant="outlined"
          margin="none"
          required
          fullWidth
          name="password"
          label="비밀번호"
          type="password"
          id="password"
          value={password}
          onChange={handlePasswordChange}
          className={classes.passwordField}
        />

        <Button
          type="submit"
          fullWidth
          variant="contained"
          color="primary"
          className={classes.submit}
          onClick={handleSubmit}
        >
          로그인
        </Button>
        <Grid container spacing={2} className={classes.buttonGroup}>
          <Grid item xs={6}>
            <Button fullWidth variant="contained" color="secondary">
              비밀번호 찾기
            </Button>
          </Grid>
          <Grid item xs={6}>
            <Button fullWidth variant="contained" color="default">
              회원가입
            </Button>
          </Grid>
        </Grid>
      </form>
    </Paper>
  );
}
